<template>
  <div style="padding: 50px">
    <table class="table table-bordered table-hover">
      <thead class="my-theme">
        <tr>
          <th>#</th>
          <th>Name</th>
          <th>Email</th>
          <th>Phone</th>
          <th>Role</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in users" :key="user" @click="userDetails">
          <td>{{ user.id }}</td>
          <td>{{ user.name }}</td>
          <td>{{ user.email }}</td>
          <td>{{ user.phone }}</td>
          <td>{{ user.role }}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { ref } from '@vue/reactivity'
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
import LogoutButton from '../components/LogoutButton.vue';
import { getCookie } from '@/utils/cookie';
import { useRouter } from 'vue-router';
import UserModel from '@/models/UserModel';

export default {
  name: 'HomeView',
  components: { LogoutButton },
  setup() {
    const router = useRouter();
    axios.defaults.headers.common['Authorization'] = `Bearer ${getCookie('token')}`;
    console.log(getCookie('token'));

    const message = ref('');
    const temp = ref([]);
    const users = ref([]);
    onMounted(async () => {
      const response = await axios.get('admin/users', {
        withCredentials: true
      });
      temp.value = response.data.data;
      
      temp.value.forEach(user => {
        if(user.role === 2 || user.role === 4) {
          users.value.push(new UserModel(user));
        }
      });
    });

    const userDetails = async (e) => {
      const id = e.target.parentElement.firstElementChild.innerText;
      router.push({
        name: 'user',
        params: {
          id: id,
        }
      });
    }
    return { message, users, userDetails }
  }
}
</script>


