export default class UserModel {
  constructor(data) {
    this.data = data;
  }
  get id() {
    return this.data.id;
  }

  get name() {
    return this.data.name;
  }

  get email() {
    return this.data.email;
  }

  get image() {
    return this.data.image_url;
  }

  get phone() {
    return this.data.phone;
  }

  get role() {
    return this.data.role;
  }

  get createdAt() {
    console.log(this.data.created_at);
    const msc = Date.parse(this.data.created_at);
    const date = new Date(msc).toLocaleString();
    return date;
  }

  get managementContracts() {
    const isEmpty = Object.keys(this.data.management_contracts).length === 0;
    return isEmpty ? null: this.data.management_contracts;
  }

  get visits() {
    return this.data.visits;
  }

  get transportContracts() {
    const isEmpty = Object.keys(this.data.transport_contracts).length === 0;
    return isEmpty ? null: this.data.transport_contracts;
  }

  get investingContracts() {
    const isEmpty = Object.keys(this.data.investing_contracts).length === 0;
    console.log(this.data.investing_contracts);
    return isEmpty ? null: this.data.investing_contracts;
  }

  get giveaways() {
    const isEmpty = Object.keys(this.data.giveaways).length === 0;
    return isEmpty ? null: this.data.giveaways;
  }

  get interests() {
    const isEmpty = Object.keys(this.data.interests).length === 0;
    return isEmpty ? null: this.data.interests;
  }
}