<template>
<div>
  <NavBar></NavBar>
    <div class="container" style="margin-top:25px">
      <div class="row">
        <div class="col">
          <img :src="exhibition.image" class="img-rounded" alt="Exhibition Image">
        </div>
        <div class="col align-items-center">
          <div>
            <h2><b>Exhibition Details</b></h2>
            Name: {{exhibition.name}}
            <br>
            Address: {{exhibition.address}}
            <br>
            Starting Date: {{exhibition.startingDate}}
            <br>
            Ending Date: {{exhibition.endingDate}}
            <br>
            Created at: {{exhibition.createdAt}}
            <br>
            Opens at: {{exhibition.opensAt}}
            <br>
            Closes at: {{exhibition.closesAt}}
            <br>
          </div>
        </div>
        <div class="col">
          <h2><b>Managers</b></h2>
          <div v-for="manager in managers" :key="manager">
         Name: {{ manager.name }}
      </div>
      <div v-if="exhibition.status != 'pending'">
        {{ exhibition.status }}
      </div>
          <div v-else>
            <select class="custom-select" @click="acceptOrReject" style="margin-top:15px">
              <option value=""></option>
              <option value="accept">Accept</option>
              <option value="reject">Reject</option>
            </select>
          </div>
        </div>
      </div>
    </div>
    <div>
      
      <h2 style="margin-top:35px"><b>Exhibition on Map</b></h2>
     <iframe
        width="95%"
        height="500"
        frameborder="0"
        scrolling="no"
        marginheight="0"
        marginwidth="0"
        :src=markerUrl
        style="
          border: 1px solid black;
          box-shadow: 0px 0px 5px 5px #888888;
        "
      >
      </iframe>
      <SideNav />
    </div>
</div>
</template>

<script>
import axios from 'axios';
import { getCookie } from '@/utils/cookie';
import { useRoute } from 'vue-router';
import ExhibitionModel from '@/models/ExhibitionModel';
import UserModel from '@/models/UserModel';
import SideNav from '@/components/SideNav.vue';
import { ref } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';
import NavBar from '@/components/NavBar.vue';
export default {
  props: ['id'],
  components: { SideNav, NavBar },
  setup() {
    const route = useRoute();
    const id = route.params.id;
    const managers = ref([]);
    const exhibition = ref({});
    const markerUrl = ref("");
    axios.defaults.headers.common['Authorization'] = `Bearer ${getCookie('token')}`;

    onMounted(async () => {
      const { data } = await axios.get(`exhibition/${id}`, {
        
        withCredentials: true
      });
      
      exhibition.value = new ExhibitionModel(data.data);
      markerUrl.value = `https://www.openstreetmap.org/export/embed.html?bbox=34.50805664062501%2C33.637489243170826%2C40.63842773437501%2C36.3106987841827&amp;&layer=mapnik&marker=${exhibition.value.lat}%2C${exhibition.value.lon}`;
      managers.value = exhibition.value.managementContracts.map(manager => {
        console.log(manager);
        return new UserModel(manager);
      });
    });

    const accept = async (e) => {
      const response = await axios.put(`admin/exhibition/${id}/accept`, {
        withCredentials: true
      }).then((e) => {
        if(e.status === 200) {
          exhibition.value.status = 'accepted'
        }
      }).catch((e) => {
        alert(`Error: ${e.message}`);
      });
    }

    const reject = async (e) => {
      const response = await axios.put(`admin/exhibition/${id}/reject`, {
        withCredentials: true
      }).then((e) => {
        if(e.status === 200) {
          exhibition.value.status = 'rejected'
        }
      }).catch((e) => {
        alert(`Error: ${e.message}`);
      });
    }

    
    const acceptOrReject = async (e) => {
      if(e.target.value === 'accept') {
        accept(e);
      } else if(e.target.value === 'reject') {
        reject(e);
      }
    }

    return  { exhibition, managers, acceptOrReject, markerUrl };
  }
}
</script>

