<template>
  <div style="padding: 50px; ">
    <table class="table table-bordered table-hover">
      <thead class="my-theme">
        <tr>
          <th>#</th>
          <th>Name</th>
          <th>Address</th>
          <th>Lon</th>
          <th>Lat</th>
          <th>Starting Date</th>
          <th>Ending Date</th>
          <th>Status</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="exhibition in exhibitions" :key="exhibition" @click="exhibitionDetails">
          <td>{{ exhibition.id }}</td>
          <td>{{ exhibition.name }}</td>
          <td>{{ exhibition.address }}</td>
          <td>{{ exhibition.lon }}</td>
          <td>{{ exhibition.lat }}</td>
          <td>{{ exhibition.startingDate }}</td>
          <td>{{ exhibition.endingDate }}</td>

          <td>
          <div v-if="exhibition.status == 'accepted' ||exhibition.status == 'published'" class="green-text">{{ exhibition.status }}</div>
          <div v-else class="red-text">{{ exhibition.status }}</div>
          </td>
        </tr>
      </tbody>
    </table>
    <div class="justify-content-center">
      <div class="row justify-content-around" style="margin: 0 300px">
        <div>
          <button class="btn" @click="prev" style="margin-right:15px">Previous</button>
          <button class="btn" @click="next" style="margin-left:15px">Next</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from '@vue/reactivity'
import { onBeforeMount, onMounted } from '@vue/runtime-core';
import axios from 'axios';
import LogoutButton from '../components/LogoutButton.vue';
import { getCookie } from '@/utils/cookie';
import { useRouter } from 'vue-router';
import ExhibitionModel from '@/models/ExhibitionModel';

export default {
  name: 'Exhibitions',
  components: { LogoutButton },
  setup() {
    const router = useRouter();
    axios.defaults.headers.common['Authorization'] = `Bearer ${getCookie('token')}`;
    console.log(getCookie('token'));

    const message = ref('');
    const temp = ref([]);
    const exhibitions = ref([]);
    let response;
    onBeforeMount(async () => {
      response = await axios.get('admin/exhibition', {
        withCredentials: true
      });
      exhibitions.value = await response.data.data.data.map(exhibition => {
        return new ExhibitionModel(exhibition);
      });
    });

    const exhibitionDetails = async (e) => {
      const id = e.target.parentElement.firstElementChild.innerText;
      router.push({
        name: 'exhibition',
        params: {
          id: id,
        }
      });
    }
    
    const prev = async () => {
      let prevPage = response.data.data.prev_page_url;
      
      if(prevPage === null) {
        return;
      }

      prevPage = prevPage.substring(26);
      console.log(prevPage);
      

      response = await axios.get(prevPage, {
        withCredentials: true
      });
      exhibitions.value = await response.data.data.data.map(exhibition => {
        return new ExhibitionModel(exhibition);
      });
    }

    const next = async () => {
      let nextPage = response.data.data.next_page_url;
      
      if(nextPage === null) {
        return;
      }

      nextPage = nextPage.substring(26);
      console.log(nextPage);

      response = await axios.get(nextPage, {
        withCredentials: true
      });
      exhibitions.value = await response.data.data.data.map(exhibition => {
        return new ExhibitionModel(exhibition);
      });
      console.log(exhibitions.value);
    }

    return { message, exhibitions, exhibitionDetails, next, prev };
  }
}
</script>


<style>
.my-theme{
  background-color: #11101d;
  color: white;
}
.red-text{
  color: red;
}
</style>
