import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginView from '../views/LoginView.vue'
import UserView from '../views/UserView.vue'
import ManagersView from '../views/ManagersView.vue'
import InvestorsView from '../views/InvestorsView.vue'
import UsersView from '../views/UsersView.vue'
import LogoutButton from '../components/LogoutButton.vue'
import ExhibitionView from '../views/ExhibitionView.vue'
import ExhibitionsView from '../views/ExhibitionsView.vue'
import PromotionRequestsView from '../views/PromotionRequestsView.vue'
import SettingsView from '../views/SettingsView.vue'
import NotificationView from '../views/NotificationView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView
  },
  {
    path: '/users',
    name: 'users',
    component: UsersView
  },
  {
    path: '/users/:id',
    name: 'user',
    component: UserView
  },
  {
    path: '/managers',
    name: 'managers',
    component: ManagersView
  },
  {
    path: '/investors',
    name: 'investors',
    component: InvestorsView
  },
  {
    path: '/promotion',
    name: 'promotion',
    component: PromotionRequestsView
  },
  {
    path: '/exhibitions',
    name: 'exhibitions',
    component: ExhibitionsView
  },
  {
    path: '/exhibitions/:id',
    name: 'exhibition',
    component: ExhibitionView
  },
  {
    path: '/notifications',
    name: 'notifications',
    component: NotificationView
  },
  {
    path: '/settings',
    name: 'settings',
    component: SettingsView
  },
  {
    path: '/logout',
    name: 'logout',
    component: LogoutButton
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
