<template>
  <div>
    <NavBar></NavBar>
    <div style="margin:35px; box-shadow: 0px 0px 10px #929292; padding: 35px;">
      <h4 style="text-align:left">Notification Sender:</h4>
      <br>
      <div>
        <input class="form-control" v-model="title" type="text" placeholder="Title" required/>
      </div>
      <div style="margin-top:15px;">
        <textarea style="height:300px;" class="form-control" v-model="body" type="text" placeholder="Body" required/>
      </div>
      <div style="margin-top:35px">
        <div>
          Select Recievers
          <br>
          <span class="check-box-style">
            <input type="checkbox" @click="addRole" value="0" />
            <span>Admins</span>
          </span>
          <span class="check-box-style">
            <input type="checkbox" @click="addRole" value="1" />
            <span>Users</span>
          </span>
          <span class="check-box-style">
            <input type="checkbox" @click="addRole" value="2" />
            <span>Investors</span>
          </span>
          <span class="check-box-style">
            <input type="checkbox" @click="addRole" value="3" />
            <span>Managers</span>
          </span>
          <span class="check-box-style">
            <input type="checkbox" @click="addRole" value="4" />
            <span>Managers And Investors</span>
          </span>
        </div>
      </div>
      <button class="my-btn-green" style="margin-top:35px" @click="submit">
        Send Notifications
      </button>
    </div>
    <SideNav />
  </div>
</template>

<script>
import axios from 'axios';
import SideNav from '@/components/SideNav.vue';
import { getCookie } from '@/utils/cookie';
import NavBar from '@/components/NavBar.vue';
export default {
  components: { SideNav, NavBar },
  data() {
    return {
      title: '',
      body: '',
      roles: [],
    }
  },
  methods: {
    async submit() {
      axios.defaults.headers.common['Authorization'] = `Bearer ${getCookie('token')}`;
      const formData = new FormData();
      formData.append('title', this.title);
      formData.append('body', this.body);
      formData.append('roles', JSON.stringify(this.roles));
      const response = await axios.post('admin/notifications', formData, {
        withCredentials: true,
      }).then((e) => {
        alert(e.data.message);
      }).catch((e) => {
        console.log(e);
        alert(e.response.data.message);
      });
    },
    addRole(e) {
      if (e.target.checked) {
        this.roles[this.roles.length] = parseInt(e.target.value);
      } else {
        this.roles.splice(this.roles.indexOf(e.target.value), 1);
      }
    },
  }
}
</script>

<style>
.check-box-style{
  margin-right: 10px;
  margin-left: 10px;
}
</style>