<template>
  <div>
    <NavBar />
    <div style="margin:35px; box-shadow: 0px 0px 10px #929292; padding: 35px;">
      <h4 style="text-align:left">Settings:</h4>
      <div class="row justify-content-center" style="margin-bottom: 25px">
        <input class="form-control" style="width: 50% !important" v-model="username" type="text" placeholder="Name"/>
        <button class="my-btn" @click="changeUsername">Change Username</button>
      </div>
      <div class="row justify-content-center" style="margin-bottom: 25px">
        <input class="form-control" style="width: 25% !important" v-model="oldPassword" type="password" placeholder="Old Password" />
        <input class="form-control" style="width: 25% !important" v-model="newPassword" @change="onChange" type="password" placeholder="New Password" />
        <button class="my-btn" @click="changePassword">Change Password</button>
      </div>
      <div>
        <input class="my-btn" type="file" accept="image" @change="uploadImage"/>
      </div>
    </div>
    <SideNav />
  </div>
</template>

<script>
import SideNav from '@/components/SideNav.vue';
import { ref } from '@vue/reactivity';
import { onMounted } from '@vue/runtime-core';
import { getCookie, setCookie } from '@/utils/cookie';
import axios from 'axios';
import NavBar from '@/components/NavBar.vue';
export default {
  components: { SideNav, NavBar },
  data() {
    return {
      oldPassword: '',
      newPassword: '',
      username: '',
      image: null,
    }
  },
  methods: {
    async changePassword() {
      const formData = new FormData();
      console.log(this.oldPassword);
      console.log(this.newPassword);
      const response = await axios.put('me/change-password', {
        'old_password': this.oldPassword,
        'new_password': this.newPassword,
      }, {
        headers: {
          'Authorization': `Bearer ${getCookie('token')}`,
        },
      }).then(response => {
        setCookie('token', '', -1);
        window.location.href = '/';
      }).catch(error => {
        alert(error);
      }).finally(() => {
        this.oldPassword = '';
        this.newPassword = '';
      });
    },
    async uploadImage(e) {
      const file = e.target.files[0];
      const formData = new FormData();
      formData.append('image', file);
      formData.append('path', 'users');
      let bodyContent = formData;

      let response = await axios.post('upload-image', bodyContent, {
        headers: {
          'Accept': 'image/*',
        }
      })
      .then(async (response) => {
        console.log(response);
        const imageUrl = response.data.data.image_url;
        console.log(imageUrl);
        const response1 = await axios.put('me', {
          'image_url': imageUrl,
          }, {
          headers: {
            'Authorization': `Bearer ${getCookie('token')}`,
          },
        }).then((response) => {
          console.log(response);
          window.location.reload();
        }).catch((error) => {
          alert(error.response.data.message);
        }).finally(() => {
          this.image = null;
        });
      }).catch((error) => {
        console.log(error);
        alert(error.response.data.message);
      });
    },
    changeUsername() {
      axios.put('me', {
        'name': this.username,
      }, {
        headers: {
          'Authorization': `Bearer ${getCookie('token')}`,
        },
      }).then((response) => {
        console.log(response);
        window.location.reload();
      }).catch((error) => {
        alert(error.response.data.message);
      }).finally(() => {
        this.username = '';
      });
    }
  }
}
</script>

<style>
.my-btn{
  background-color: #f5f5f5;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  width: 20%; 
  color: #333;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857;
  margin-bottom: 0;
  padding: 6px 12px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
}

.my-btn:hover {
  background-color: #a0fab8;
  border-color: #0aad6c;
  color: rgb(12, 110, 33);
}
</style>