<template>
  <div style="padding: 50px">
    <table class="table table-bordered table-hover">
      <thead class="my-theme">
        <tr>
          <th>#</th>
          <th>User ID</th>
          <th>Bank Account</th>
          <th>Address</th>
          <th>Job</th>
          <th>Company</th>
          <th>Role</th>
          <th>Status</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="promotionRequest in promotionRequests" :key="promotionRequest">
          <td>{{ promotionRequest.id }}</td>
          <td>{{ promotionRequest.userId }}</td>
          <td>{{ promotionRequest.bankAccount }}</td>
          <td>{{ promotionRequest.address }}</td>
          <td>{{ promotionRequest.job }}</td>
          <td>{{ promotionRequest.company }}</td>
          <td>{{ promotionRequest.role }}</td>
          <td v-if="promotionRequest.status == 'pending'">
            <select class="custom-select" @click="acceptOrReject">
              <option value=""></option>
              <option value="accept">Accept</option>
              <option value="reject">Reject</option>
            </select>

          </td>
          <td v-else>
            <div v-if="promotionRequest.status == 'promoted'" class="green-text">
            {{ promotionRequest.status }}
            </div>
            <div v-else class="red-text">
            {{ promotionRequest.status }}
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { ref } from '@vue/reactivity'
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
import { getCookie } from '@/utils/cookie';
import { useRouter } from 'vue-router';
import SideNav from "@/components/SideNav.vue";
import PromotionRequestModel from '@/models/PromotionRequestModel';
export default {
  name: 'PromotionRequestsView',
  components: { SideNav },
  setup() {
    const router = useRouter();
    const promotionRequests = ref([]);
    axios.defaults.headers.common['Authorization'] = `Bearer ${getCookie('token')}`;

    onMounted(async () => {
      const { data } = await axios.get('promotion', {
        withCredentials: true
      });
      // promotionRequests.value = data.data;
      promotionRequests.value = data.data.map(promotionRequest => {
        return new PromotionRequestModel(promotionRequest);
      });
    });

    const accept = async (id) => {
      console.log('accepting..');
      const response = await axios.put(`promotion/accept/${id}`, {
        withCredentials: true
      }).then((e) => {
        if(e.status === 200) {
          promotionRequests.value[id - 1].status = 'promoted'
        }
      }).catch((e) => {
        alert(`Error: ${e.message}`);
      });
    }

    const reject = async (id) => {
      console.log('rejecting..');
      const response = await axios.put(`promotion/reject/${id}`, {
        withCredentials: true
      }).then((e) => {
        if(e.status === 200) {
          promotionRequests.value[id - 1].status = 'rejected'
        }
      }).catch((e) => {
        alert(`Error: ${e.message}`);
      });
    }

    const acceptOrReject = async (e) => {
      const id = e.target.parentElement.parentElement.firstElementChild.innerText;
      const status = e.target.value;
      console.log(status);

      if(status == 'accept') {
        accept(id);
      } else if(status == 'reject') {
        reject(id);
      } else {
        return;
      }
    }

    return { promotionRequests, acceptOrReject };
  }
}
</script>


<style>
.custom-select{
  width: 100px;
}

</style>