export default class PromotionRequestModel {
  constructor(data) {
    this.data = data;
  }

  get id() {
    return this.data.id;
  }

  get userId() {
    return this.data.user_id;
  }

  get bankAccount() {
    return this.data.bank_account;
  }

  get address() {
    return this.data.address;
  }

  get job() {
    return this.data.job;
  }
  
  get company() {
    return this.data.company;
  }

  get role() {
    return this.data.role;
  }

  get status() {
    return this.data.status;
  }

  get createdAt() {
    const msc = Date.parse(this.data.created_at);
    const date = new Date(msc).toLocaleString();
    return date;
  }

  set status(status) {
    this.data.status = status;
  }
}