<template>
  <div>
    <NavBar></NavBar>
    <h1 style="text-align: left; margin-left: 25px; margin-top: 15px;">User Profile</h1>
    <SideNav />
    <div class="container" style="margin-top:35px">
      <div class="row">
        <div class="col-sm-2">
           <img :src="user.image" :alt="user.name" class="avatar">
        </div>
        <div class="col-sm-2">
        </div>
        <div class="col-sm-8" style="text-align: left;">
          <h1>{{ user.name }}</h1>
          <h4>Email: {{ user.email }}</h4>
          <h5>Phone: {{ user.phone }}</h5>
          <h5 style="margin-bottom: 50px">Role: {{ user.role }}</h5>
          <h5 v-if="user.interests">
            Interests:
            <br>
            <li v-for="interest in user.interests" :key="interest" @click="exhibition(interest.exhibition_id)">
              Exhibition Name: {{ interest.name }}
            </li>
          </h5>
          <h5 v-if="user.giveaways">
            Giveaways:
            <p v-for="giveaway in user.giveaways" :key="giveaway">
              {{ giveaway }}
            </p>
          </h5>
          <h5 v-if="user.managementContracts">
            Management Contracts: 
            <br>
            <li v-for="contract in user.managementContracts" :key="contract" @click="exhibition(contract.id)">
              Exhibition Name: {{ contract.name }}
            </li>
          </h5>
          <h5 v-if="user.investingContracts">
            Investing Contracts: 
            <br>
            <li v-for="contract in user.investingContracts" :key="contract" style="" @click="exhibition(contract.exhibition_id)">
              Exhibition Name: {{ contract.name }}
            </li>
          </h5>
          <h5 v-if="user.transportContracts">
            Transport Contracts: 
            <br>
            <h6 v-for="contract in user.transportContracts" :key="contract" @click="exhibition(contract.exhibition_id)">
              ID: {{ contract.id }}
              <br>
              Exhibition ID: {{ contract.exhibition_id }}
            </h6>
          </h5>
        </div>
      </div>
    </div>    
  </div>
</template>

<script>
import { onMounted, ref } from '@vue/runtime-core';
import { useRoute, useRouter } from 'vue-router';
import axios from 'axios';
import { getCookie } from '@/utils/cookie';
import SideNav from '@/components/SideNav.vue';
import UserModel from '@/models/UserModel';
import NavBar from '@/components/NavBar.vue';
export default {
  // get user from props
  props: ['id'],
  components: { SideNav, NavBar },
  setup() {
    const route = useRoute();
    const router = useRouter();
    const id = route.params.id;
    const user = ref({});
    axios.defaults.headers.common['Authorization'] = `Bearer ${getCookie('token')}`;

    onMounted(async () => {
      const { data } = await axios.get(`admin/users/${id}`, {
        withCredentials: true
      });
      // user.value = data.data;
      user.value = new UserModel(data.data);
    });

    const exhibition = (id) => {
      router.push(`/exhibitions/${id}`);
    }
    return  { user, exhibition };
  }

}
</script>

<style>
  img {
    border-radius: 50%;
    width: 200px;
    height: 200px;
  }

  li:hover {
    color: #9e50a8;
  }
</style>