<template>
  <main class="form-signin d-flex justify-content-center align-items-center" style="margin-top: 100px;">
    <form @submit.prevent="submit">
      <div class="card">
        <h1 class="padding">Expoverse</h1>
        <div class="col">
          <label class="custom-field card-padding" for="email">
            <input class="form-control" type="text" placeholder="Email" name="email" id="email" value="admin@expoverse.com">
          </label>
          <label class="custom-field card-padding" for="password">
            <input class="form-control" type="password" placeholder="Password" name="password" id="password" value="password">
          </label>
          <div class="d-flex card-padding left">
            <input type="checkbox" class="hidden-xs-up" id="cbx" v-model="remember">
            <label class="cbx" for="cbx"></label>
            <label class="lbl" for="cbx">Remember me</label> 
          </div>
          
            <button type="submit" class="btn btn-primary" @click="login">Login</button>
          
        </div>
      </div>
    </form>
  </main>
</template>

<script>
import axios from 'axios';
import { useRouter } from 'vue-router';
import { setCookie, getCookie } from '@/utils/cookie';
import UserModel from '@/models/UserModel';

export default {
  name: 'LoginCard',
  props: ['remember'],
  setup(props) {
    const router = useRouter();

    const submit = async e =>  {
      const form = new FormData(e.target);

      const inputs = Object.fromEntries(form.entries());
      const data = await axios.post('auth/login', inputs, { withCredentials: true }).then((e) => {
        const user = new UserModel(e.data.data.user);
        const token = e.data.data.token;
        if(user.role != 0) {
          alert('You are not an admin');
          return;
        }

        if(props.remember) {
          setCookie('token', token, 1);
        } else {
          setCookie('token', token, 1/24);
        }

        console.log(JSON.stringify(user));
        axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
        console.log(axios.defaults.headers.common['Authorization']);
        // document.cookie =`${data.data.token}`;
        router.push('/');
      }).catch(err => {
        alert(err.response.data.message); 
      });
    }
    return { submit };
  }
}
</script>

<style>
  .btn {
    margin-top: 10px;
    height: 50px;
    width: 125px;
    background-color: #11101d !important;
    border-radius: 5px !important;
    font-weight: bold !important;
    color: #fff !important;
  }

  .left {
    padding-left: 60px !important;
  }

  .card-padding {
    padding-bottom: 10px;
  }

  .padding {
    padding-top: 30px;
    padding-bottom: 20px;
  }

  .card {
    background: grey;
    border-radius: 20px !important;
    width: 400px;
    max-width: 400px;
    height: 400px;
    /* min-height: 400px; */
    box-shadow: 0 2px 43px -4px rgb(0 0 0 / 19%);
  }
  *, *::before, *::after {
    box-sizing: border-box;
  }

  /* .custom-field::backdrop {
    content: attr(aria-label);
  } */

  .custom-field {
    font-size: 14px;
    position: relative;
  }

  .custom-field input {
    border: none;
    -webkit-appearance: none;
    -ms-appearance: none;
    -moz-appearance: none;
    appearance: none;
    background: transparent;
    padding: 12px;
    border-radius: 10px;
    border: 2px solid #e6e6e6;
    width: 250px;
    height: 50px;
    font-size: 14px;
  }

  .custom-field .placeholder {
    position: absolute;
    width: 100%;
    left: 12px;
    bottom: 50%;
    top: 22px;  
    transform: translateY(-50%);
    width: calc(100% - 24px);  
    color: #aaa;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .hidden-xs-up {
      display: none !important;
  }
  .cbx {
    position: relative;
    top: 1px;
    width: 17px;
    height: 17px;
    border: 1px solid #c8ccd4;
    border-radius: 3px;
    vertical-align: middle;
    transition: background 0.1s ease;
    cursor: pointer;
  }
  .cbx:after {
    content: '';
    position: absolute;
    top: 1px;
    left: 5px;
    width: 5px;
    height: 11px;
    opacity: 0;
    transform: rotate(45deg) scale(0);
    border-right: 2px solid #fff;
    border-bottom: 2px solid #fff;
    transition: all 0.3s ease;
    transition-delay: 0.15s;
  }
  .lbl {
    margin-left: 5px;
    vertical-align: middle;
    cursor: pointer;
  }
  #cbx:checked ~ .cbx {
    border-color: transparent;
    background: #6871f1;
    animation: jelly 0.6s ease;
  }
  #cbx:checked ~ .cbx:after {
    opacity: 1;
    transform: rotate(45deg) scale(1);
  }
  .cntr {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    text-align: center;
  }
  @-moz-keyframes jelly {
    from {
      transform: scale(1, 1);
    }
    30% {
      transform: scale(1.25, 0.75);
    }
    40% {
      transform: scale(0.75, 1.25);
    }
    50% {
      transform: scale(1.15, 0.85);
    }
    65% {
      transform: scale(0.95, 1.05);
    }
    75% {
      transform: scale(1.05, 0.95);
    }
    to {
      transform: scale(1, 1);
    }
  }
  @-webkit-keyframes jelly {
    from {
      transform: scale(1, 1);
    }
    30% {
      transform: scale(1.25, 0.75);
    }
    40% {
      transform: scale(0.75, 1.25);
    }
    50% {
      transform: scale(1.15, 0.85);
    }
    65% {
      transform: scale(0.95, 1.05);
    }
    75% {
      transform: scale(1.05, 0.95);
    }
    to {
      transform: scale(1, 1);
    }
  }
  @-o-keyframes jelly {
    from {
      transform: scale(1, 1);
    }
    30% {
      transform: scale(1.25, 0.75);
    }
    40% {
      transform: scale(0.75, 1.25);
    }
    50% {
      transform: scale(1.15, 0.85);
    }
    65% {
      transform: scale(0.95, 1.05);
    }
    75% {
      transform: scale(1.05, 0.95);
    }
    to {
      transform: scale(1, 1);
    }
  }
  @keyframes jelly {
    from {
      transform: scale(1, 1);
    }
    30% {
      transform: scale(1.25, 0.75);
    }
    40% {
      transform: scale(0.75, 1.25);
    }
    50% {
      transform: scale(1.15, 0.85);
    }
    65% {
      transform: scale(0.95, 1.05);
    }
    75% {
      transform: scale(1.05, 0.95);
    }
    to {
      transform: scale(1, 1);
    }
  }
</style>