<template>
  <div style="padding: 50px">
    <table class="table table-bordered table-hover">
      <thead class="my-theme">
        <tr>
          <th>#</th>
          <th>Name</th>
          <th>Email</th>
          <th>Phone</th>
          <th>Role</th>
          <th>Created At</th>
          <th>Promote to Admin</th>
          <th>Ban</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="user in users" :key="user">
          <td @click="userDetails">{{ user.id }}</td>
          <td @click="userDetails">{{ user.name }}</td>
          <td @click="userDetails">{{ user.email }}</td>
          <td @click="userDetails">{{ user.phone }}</td>
          <td @click="userDetails">{{ user.role }}</td>
          <td @click="userDetails">{{ user.createdAt }}</td>
          <td>
            <div v-if="user.role != 0 && user.role != -1">
              <button class="my-btn-green" @click="promoteToAdmin(user.id)">Promote</button>
            </div>
            <div class="red-text" v-else-if="user.role == -1">
              Banned
            </div>
            <div class="green-text" v-else>
              Admin
            </div>
          </td>
          <td>
            <div v-if="user.role != -1">
              <button class="my-btn-red" @click="banUser(user.id)">Ban</button>
            </div>
            <div v-else>
              <button class="my-btn-red" @click="unbanUser(user.id)">
                Unban
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { ref } from '@vue/reactivity'
import { onMounted } from '@vue/runtime-core';
import axios from 'axios';
import LogoutButton from '../components/LogoutButton.vue';
import { getCookie } from '@/utils/cookie';
import { useRouter } from 'vue-router';
import UserModel from '@/models/UserModel';

export default {
  name: 'HomeView',
  components: { LogoutButton },
  setup() {
    const router = useRouter();
    axios.defaults.headers.common['Authorization'] = `Bearer ${getCookie('token')}`;
    console.log(getCookie('token'));

    const message = ref('');
    const users = ref([]);
    onMounted(async () => {
      const { data } = await axios.get('me', {
        withCredentials: true
      });
      message.value = `${data.data.name}`; 

      const response = await axios.get('admin/users', {
        withCredentials: true
      });

      users.value = response.data.data.map(user => {
        return new UserModel(user);
      });
      console.log(users.value);
    });

    const userDetails = async (e) => {
      const id = e.target.parentElement.firstElementChild.innerText;
      router.push({
        name: 'user',
        params: {
          id: id,
        }
      });
    }

    const promoteToAdmin = async (id) => {
      const answer = confirm('Are you sure you want to promote this user to admin?')
      if(answer) {
        const data = await axios.put(`admin/users/${id}/promote`, {
          withCredentials: true
        }).then((e) => {
          console.log(users);
          users.value[id - 1].data.role = 0;
        }).catch(err => {
          alert(err);
        });
      }
    }

    const banUser = async (id) => {
      const answer = confirm('Are you sure you want to ban this user?')
      if(answer) {
        const data = await axios.put(`admin/user/${id}/ban`, {
          withCredentials: true
        }).then((e) => {
          users.value[id - 1].data.role = -1;
        }).catch(err => {
          alert(err);
        });
      }
    }

    const unbanUser = async (id) => {
      const answer = confirm('Are you sure you want to unban this user?')
      if(answer) {
        const data = await axios.put(`admin/user/${id}/unban`, {
          withCredentials: true
        }).then((e) => {
          users.value[id - 1].data.role = 1;
        }).catch(err => {
          alert(err);
        });
      }
    }

    return { message, users, userDetails, promoteToAdmin, banUser, unbanUser };
  }
}
</script>


<style>
.my-btn-green{
  background-color: #f5f5f5;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  color: #333;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857;
  margin-bottom: 0;
  padding: 6px 12px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  width: 100%;
}
/* .my-btn-green on hover green color */
.my-btn-green:hover {
  background-color: #a0fab8;
  border-color: #0aad6c;
  color: rgb(12, 110, 33);
}
.my-btn-red:hover{
  background-color: #faa0a0;
  border-color: #ad0a0a;
  color: rgb(110, 12, 12);
}
table{
  box-shadow: 0px 0px 10px #929292;
}

.my-btn-red{
  background-color: #f5f5f5;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  color: #333;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857;
  margin-bottom: 0;
  padding: 6px 12px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  width: 100%;
}
.green-text{
  color: #0aad6c;
}
.red-text{
  color: #ad0a0a;
}
</style>