export default class ExhibitionModel {
  constructor(data) {
    this.data = data;
  }

  get id() {
    return this.data.id;
  }

  get name() {
    return this.data.name;
  }

  get address() {
    return this.data.address;
  }

  get lon() {
    return this.data.lon;
  }

  get lat() {
    return this.data.lat;
  }

  get startingDate() {
    return this.data.starting_date;
  }

  get endingDate() {
    return this.data.ending_date;
  }

  get status() {
    return this.data.status;
  }

  get createdAt() {
    const msc = Date.parse(this.data.created_at);
    const date = new Date(msc).toLocaleString();
    return date;
  }

  get image() {
    return this.data.image_url;
  }

  get opensAt() {
    return this.data.opens_at;
  }

  get closesAt() {
    return this.data.closes_at;
  }

  get managementContracts() {
    return this.data.management_contracts
  }
    get imageUrl() {
    return this.data.image_url;
  } 
  set status(status) {
    this.data.status = status;
  }
}